    'dark-white': $dark-white,
    'black': $black,
    'gray': $gray,
    'second-gray': $second-gray,
    'second-black': $second-black,

    'primary': $primary,
    'secondary': $secondary,
    'error': $error,
    'success': $success,
    'disabled': $disabled,
    'text': $text,
    'sub-text': $sub-text,
    'night': $night,
    'off-white': $off-white,
    'gradient': $gradient,
    'green-gradient': $green-gradient,
